import React, { useState } from "react";
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Modal,
  Select,
  Typography,
} from "@mui/material";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
};

const OrderModal = ({ open, onClose }) => {
  const [dj, setDj] = useState("dj1");
  const [host, setHost] = useState("host1");
  const [mc, setMc] = useState("mc1");
  const [artist, setArtist] = useState("artist1");
  const [hoursCount, setHoursCount] = useState(5);

  const handleSubmit = () => {
    console.log("Order confirmed with", { dj, host, mc, artist, hoursCount });
    onClose();
  };

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={style}>
        <Typography variant="h6"> </Typography>

        <FormControl fullWidth margin="normal">
          <InputLabel id="dj-label"> </InputLabel>
          <Select
            labelId="dj-label"
            value={dj}
            onChange={(e) => setDj(e.target.value)}
          >
            <MenuItem value="dj1"> 1 (5000 )</MenuItem>
            <MenuItem value="dj2"> 2 (6000 )</MenuItem>
            <MenuItem value="dj3"> 3 (7000 )</MenuItem>
          </Select>
        </FormControl>

        <FormControl fullWidth margin="normal">
          <InputLabel id="host-label"> </InputLabel>
          <Select
            labelId="host-label"
            value={host}
            onChange={(e) => setHost(e.target.value)}
          >
            <MenuItem value="host1"> 1 (7000 )</MenuItem>
            <MenuItem value="host2"> 2 (8000 )</MenuItem>
            <MenuItem value="host3"> 3 (9000 )</MenuItem>
          </Select>
        </FormControl>

        <FormControl fullWidth margin="normal">
          <InputLabel id="mc-label"> </InputLabel>
          <Select
            labelId="mc-label"
            value={mc}
            onChange={(e) => setMc(e.target.value)}
          >
            <MenuItem value="mc1"> 1 (6000 )</MenuItem>
            <MenuItem value="mc2"> 2 (7000 )</MenuItem>
            <MenuItem value="mc3"> 3 (8000 )</MenuItem>
          </Select>
        </FormControl>

        <FormControl fullWidth margin="normal">
          <InputLabel id="artist-label"> </InputLabel>
          <Select
            labelId="artist-label"
            value={artist}
            onChange={(e) => setArtist(e.target.value)}
          >
            <MenuItem value="artist1"> 1 (8000 )</MenuItem>
            <MenuItem value="artist2"> 2 (9000 )</MenuItem>
            <MenuItem value="artist3"> 3 (10000 )</MenuItem>
          </Select>
        </FormControl>

        <FormControl fullWidth margin="normal">
          <InputLabel htmlFor="hoursCount"> </InputLabel>
          <input
            type="number"
            id="hoursCount"
            value={hoursCount}
            min="1"
            onChange={(e) => setHoursCount(e.target.value)}
            className="form-control"
          />
        </FormControl>

        <Button onClick={handleSubmit} variant="contained" color="primary">
           
        </Button>
        <div className="social-buttons">
          <a
            href="https://t.me/your_telegram"
            target="_blank"
            className="social-button telegram"
          >
            <i className="fab fa-telegram-plane"></i>   Telegram
          </a>
          <a
            href="https://wa.me/your_whatsapp_number"
            target="_blank"
            className="social-button whatsapp"
          >
            <i className="fab fa-whatsapp"></i>   WhatsApp
          </a>
        </div>
      </Box>
    </Modal>
  );
};

export default OrderModal;
